import { InteractionType } from '@azure/msal-browser';
import { AuthenticatedTemplate, UnauthenticatedTemplate, useIsAuthenticated, useMsalAuthentication } from '@azure/msal-react';
import React, { useEffect, useState } from 'react';
import './App.css';
import MyChart from './components/chart';
import { msalAuthenticationRequest } from './constants';
import { SpotPriceClient } from './Utils/SpotPriceClient';

function App() {
  const [chartData, setChartData] = useState<any>();
  const { result, error } = useMsalAuthentication(InteractionType.Redirect, msalAuthenticationRequest);
  const isAuthenticated = useIsAuthenticated();

  useEffect(() => {
    if (isAuthenticated) {
      console.log(result);
    }
    if (error) {
      console.error(error);
    }
  }, [isAuthenticated, result, error])

  useEffect(() => {
    if (!isAuthenticated) return;

    SpotPriceClient.getSpotPrices()
      .then((res) => {

        const now = new Date(Date.now());
        const todayStart = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 0, 0, 0, 0);
        console.log("todayStart: " + todayStart);
        const todayEnd = new Date(now.getFullYear(), now.getMonth(), now.getDate(), 23, 0, 0, 0);
        const yesterday = new Date(new Date(new Date().setDate(now.getDate() - 1)).setHours(0));

        console.log("res.ok: " + res.success);
        const map = res.result.records.map(d => ({ HourDK: new Date(d.HourDK), PriceArea: d.PriceArea, SpotPriceDKK: d.SpotPriceDKK / 1000.0, SpotPriceEUR: d.SpotPriceEUR / 1000.0 }))
        const relevantData = map.filter(d => d.HourDK >= yesterday);
        const ordered = relevantData.sort((a, b) => a.HourDK.getTime() - b.HourDK.getTime());
        const filter = ordered.filter(d => d.PriceArea === 'DK2');
        const lastElementWithPrice = filter
          .filter(d => d.SpotPriceDKK !== 0 && d.SpotPriceEUR !== 0)
          //.at(-1);
          .slice(-1)[0];

        const eurToDkkConversionRate = lastElementWithPrice
          ? lastElementWithPrice.SpotPriceDKK / lastElementWithPrice.SpotPriceEUR
          : 7.5;


        const todaysData = filter.filter(l => new Date(l.HourDK) >= todayStart && new Date(l.HourDK) <= todayEnd);
        const todaysPrices = todaysData.map(l => l.SpotPriceDKK > 0 ? l.SpotPriceDKK : l.SpotPriceEUR * eurToDkkConversionRate);
        const todaysAvaragePrice = todaysPrices.reduce((partialSum, a) => partialSum + a, 0) / todaysPrices.length;

        const yesterdayAvaragePrice = filter
          .filter(l => new Date(l.HourDK) < todayStart)
          .map(l => l.SpotPriceDKK > 0 ? l.SpotPriceDKK : l.SpotPriceEUR * eurToDkkConversionRate)
          .reduce((partialSum, a) => partialSum + a, 0) / filter.filter(l => new Date(l.HourDK) < todayStart).length;

        const tomorrowAvaragePrice = filter
          .filter(l => new Date(l.HourDK) > todayEnd)
          .map(l => l.SpotPriceDKK > 0 ? l.SpotPriceDKK : l.SpotPriceEUR * eurToDkkConversionRate)
          .reduce((partialSum, a) => partialSum + a, 0) / filter.filter(l => new Date(l.HourDK) > todayEnd).length;

        console.log(`eurToDkkConversionRate: ${eurToDkkConversionRate}`);
        console.log(JSON.stringify(filter));

        setChartData({
          labels: filter.map(l => l.HourDK.toDateString() + ` Kl: ${l.HourDK.getHours()}`),
          datasets: [
            {
              type: 'line' as const,
              label: 'gen. snit',
              borderColor: 'rgb(255, 99, 132)',
              borderWidth: 1,
              fill: false,
              data: filter.map((l) => {
                if (new Date(l.HourDK) < todayStart) return yesterdayAvaragePrice;
                else if (new Date(l.HourDK) > todayEnd) return tomorrowAvaragePrice;
                else if (new Date(l.HourDK) >= todayStart && new Date(l.HourDK) <= todayEnd) return todaysAvaragePrice;
                else return -1;
              }),
            },
            {
              type: 'bar' as const,
              label: 'Pris DKK',
              data: filter.map(l => l.SpotPriceDKK > 0 ? l.SpotPriceDKK : l.SpotPriceEUR * eurToDkkConversionRate),
              backgroundColor: filter.map(l => {
                const local = l.HourDK;
                if (local.getDate() === now.getDate() && local.getHours() === now.getHours()) {
                  // console.log("Debug: this hour");
                  return 'rgba(54, 162, 235, 1)';
                }
                else if (local.getDate() === now.getDate()) {
                  // console.log("Debug: today");
                  return 'rgba(54, 162, 235, 0.2)';
                }
                else if (local.getDate() >= now.getDate()) {
                  // console.log("Debug: tomorrow");
                  return 'rgba(255, 99, 132, 0.2)';
                }
                else {
                  // console.log(`Debug: ${local.getDate()} | ${now.getDate()}`);
                  // console.log("Debug: yesterday");
                  return 'rgba(255, 159, 64, 0.2)';
                }

              }),
              borderColor: filter.map(l => {
                const local = new Date(l.HourDK);
                if (local.getDay() === now.getDay())
                  return 'rgba(54, 162, 235, 1)';
                else if (local.getDay() >= now.getDay())
                  return 'rgba(255, 99, 132, 1)';
                return 'rgba(255, 159, 64, 1)';

              }),
              borderWidth: 1
            }]
        });
      });
  }, [isAuthenticated]);

  return (
    <div className="App">
      <AuthenticatedTemplate>
        {chartData && <MyChart data={chartData} />}
      </AuthenticatedTemplate>
      <UnauthenticatedTemplate>
        <p>need to signed in!</p>
      </UnauthenticatedTemplate>
    </div>
  );
}

export default App;
